import back from '@/components/back'
import $load from '@/utils/loading'
export default {
    components: {
        back,
    },
    data(){
        return{
            PatRequireOrderId: this.$route.query.PatRequireOrderId || 1, //需求单Id
            title: '购药订单',
            user: this.$store.state.userInfo,
            info: {
                'PatientName': '', //就诊人姓名
                'PatientSex': '', //就诊人性别
                'PatientAge': '', //就诊人年龄
                'PatientAgeNum': '', //转化后的就诊人年龄
                'CreateTime': '', //创建时间
                'DrugAllergy': '', //药物过敏史
                'HealthHistory': '', //既往病史
                'IsPregnant': null, //是否怀孕
                'IsUsedDrugs': null, //是否曾用药
                'Diagnoses': '', //疾病诊断
                'MedicalImgList': [], //病历信息图片
                'MedicationDetails': [], //药品明细
                'AssistantId': '', //助理id
                'Status': null, //需求单状态 0：待处理，1：医生处理中，2：已开处方/完成，3：审方中，4：审方被驳回
            },
            showConfirm1: false, //补充资料弹窗
            showConfirm2: false, //驳回弹窗
            materialReasonList: [], //补充资料理由
            materialReason: '', //补充资料手动填写理由
            rejectReasonList: [], //驳回理由
            rejectReason: '', //驳回手动填写理由
            defaultImg: 'this.src="' + require('../../assets/images/icon0810_01.png') + '"', //头像默认图片
            defaultImg2: 'this.src="' + require('../../assets/images/prescriptionList.png') + '"', //病历信息默认图片
        }
    },
    created(){
        this.getInfo()
    },
    mounted() {
        
    },
    methods:{
        getInfo(){ //获取需求单详情
            this.$get('Prescription/SellingMedication/GetRequireOrder',{
			    PatRequireOrderId: this.PatRequireOrderId
			}).then(res => {
                res.PatientAgeNum = res.PatientAge.split('岁')[0];
                this.info = res;
			})
        },
        toPreview(arr,idx){ //点击查看大图
            if(arr[idx]){
                const data = {
                    position: idx,
                    url: arr
                }
                this.$bridge.callhandler('Preview', JSON.stringify(data))
            }
        },
        onConfirm1(){ //补充资料
            if(this.materialReasonList.length<=0 && !this.materialReason){
                this.$toast("请选择或填写理由");
                return;
            }
            //判断是否有手动输入的理由
            if(this.materialReason){
                this.materialReasonList.push(this.materialReason)
            }
            
			this.$post('Prescription/SellingMedication/RemandToAdditionalInfo',{
				PatRequireOrderId:this.PatRequireOrderId,
				Reasons:this.materialReasonList,
			}).then(o => {
                this.materialReasonList = [];
                this.materialReason = '';
			    this.$toast("发送成功")
			}).catch(err => {
                this.materialReasonList = [];
                this.materialReason = '';
			    this.$toast("发送失败,请重试")
            })
        },
        // onConfirm2(){ //驳回
        //     console.log(this.rejectReason);
        //     return false;
		// 	this.$post('PlatFormAPI/SpecialistHosGroup/SaveSpecialistReferral',{
		// 		SpecialistHosGroupId:this.$route.query.SpecialistHosGroupId,
		// 		HosGroupName:this.$route.query.HosGroupName,
		// 	    FromDrId: this.user.User.DoctorId,//来源医生Id(当前登录医生，必填)
		// 		FromDrName:this.user.User.DoctorName,
		// 		ToDrId: this.ToDrId,
		// 		ToDrName: this.ToDrName,
		// 		ReferralRemark:this.referralRemark,	//转诊说明
		// 		PatientId:this.$route.query.PatientId, //会员Id(必填)
		// 		PatientName: this.$route.query.PatientName,	//会员姓名(必填)
		// 	}).then(o => {
		// 	   this.$toast("转诊成功")
		// 	   setTimeout(() => {
		// 			this.$router.push('/teamList')
		// 	   }, 2000)
		// 	   this.$bridge.callhandler('ZhuanZhenSuccess', true)
		// 	})
		// },
    }
}



